import { Component, Injector, OnInit} from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { Batch } from 'src/app/interface/batch/batch';
import { BaseForm } from 'src/app/class/base.form';
import { PartialSubmit } from 'src/app/interface/common/partial-submit';

@Component({
  selector: 'app-generate-batch',
  templateUrl: './generate-batch.component.html',
  styleUrls: ['./generate-batch.component.css']
})
export class GenerateBatchComponent extends BaseForm implements OnInit {

  public batchNumber : any | Batch;
  public name : any;

  constructor(injector : Injector) {
    super(injector);
    this.param.endpoint = URLz.BATCH_NUM
    let user_Data;
    if(localStorage.getItem('permission_data_server') != null){
       user_Data = JSON.parse(atob(localStorage.getItem('permission_data_server')));
       this.name = user_Data.profile.name;
    }
  }

  ngOnInit(): void {
    this.getBatch();
  }

  initform() {
    this._fs._form = this._fb.group({
      batch_num: [''],
    });
  }

  getBatch(){
    this._http.get({endpoint: URLz.BATCH_NUM}).subscribe((res : any)=>{
        this.batchNumber  = res.data.row.batch_number;
    })
  }

  _onSubmity(ps: PartialSubmit = this._fss.defaultBehaviour) {
    super._onSubmity({
      body: (ps) => {
        ps.param.body = this._fs._form.value;
      },
      next: (ps, res) => {
        this.batchNumber = res.data.batch_number;
      }
    })
  }


}
